import parse from "html-react-parser";
import { useRef, useEffect, useState } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

function InlineFoldout(props) {
  const { name, position, text } = props;
  const [expanded, setExpanded] = useState(false);
  let onClick = () => {
    setExpanded(!expanded);
  };

  return (
    <div
      className={`inline-foldout ${expanded ? "is-expanded" : ""}`}
      onClick={onClick}
      role="dialog"
    >
      <div className="inline-foldout__meta">
        <span className="meta__name">{name}</span>
        <span className="meta__position">{position}</span>
      </div>
      <div className="inline-foldout__panel foldout-panel">
        <div className="foldout-panel__inner">
          <div
            className="foldout-panel__wrapper"
            dangerouslySetInnerHTML={{ __html: text }}
          ></div>
        </div>
        <div className="foldout-panel__footer">
          <button>Read more</button>
        </div>
      </div>
    </div>
  );
}

function ElementStory(props) {
  const [expanded, setExpanded] = useState(false);
  let onClick = () => {
    setExpanded(!expanded);
  };
  const refBody = useRef();

  let getLabel = () => {
    if (expanded) {
      return "Close";
    } else {
      return "Read more";
    }
  };

  useEffect(() => {
    if (expanded) {
      // let expandedheight =
      // gsap.set(refBody.current, { height: refBody.current.scrollHeight });
      gsap.to(refBody.current, { height: refBody.current.scrollHeight });
    } else {
      gsap.to(refBody.current, { height: "0px" });
    }
  }, [expanded]);

  // console.log(props);
  return (
    <section className="component component--story container">
      <article className={`inline-story ${expanded ? "is-expanded" : ""}`}>
        <header>
          <h2>{props.title}</h2>
          <h3>{props.intro}</h3>
        </header>
        <section className="inline-story__body" ref={refBody}>
          <div className="text">{parse(props.text)}</div>
        </section>
        <footer>
          <button onClick={onClick}>{getLabel()}</button>
        </footer>
      </article>
    </section>
  );
}

export default ElementStory;
