import React from "react";
import { ReactComponent as IconBurger } from "../assets/icon-burger.svg";
import { ReactComponent as IconDownload } from "../assets/icon-download.svg";
import { ReactComponent as IconDarkmode } from "../assets/icon-darkmode.svg";
import { ReactComponent as IconArrowDown } from "../assets/icon-arrow-down.svg";
import { ReactComponent as IconHand } from "../assets/icon-hand.svg";
import { ReactComponent as IconClose } from "../assets/icon-close.svg";

function Icon(props) {
  let getIcon = (icon) => {
    switch (icon) {
      case "arrow-down":
        return <IconArrowDown></IconArrowDown>;
        break;
      case "burger":
        return <IconBurger></IconBurger>;
        break;
      case "darkmode":
        return <IconDarkmode></IconDarkmode>;
        break;
      case "download":
        return <IconDownload></IconDownload>;
        break;
      case "hand":
        return <IconHand></IconHand>;
        break;
      case "close":
        return <IconClose></IconClose>;
        break;
    }
  };
  return <figure className="icon">{getIcon(props.icon)}</figure>;
}

export default Icon;
