// import Example from "./Example";
import { Transition } from "react-transition-group";
import React, { useState, useEffect, useRef } from "react";
import { useDispatch } from "reactn";

import BookChaptersUI from "./BookChaptersUI";
import { useGlobal } from "reactn";
import gsap from "gsap/gsap-core";
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks
} from "body-scroll-lock";
import Icon from "components/Icon";

function OverlayMenu() {
  const [showMenu, setShowMenu] = useGlobal("showMobileOverlayMenu");
  const ref = useRef(null);
  const activateBlur = useDispatch("blur");
  const [darkModeEnabled] = useGlobal("darkMode");
  const refDarkMode = useRef();

  const toggleDarkMode = useDispatch("toggleDarkMode");

  let onClose = () => {
    // if (showMenu) {
    setShowMenu(false);
    // }
  };

  let onEnter = () => {
    gsap.set(ref.current, { y: "0%", opacity: 0 });
    gsap.to(ref.current, { opacity: 1 });
  };
  let onExit = () => {
    gsap.to(ref.current, {
      opacity: 0,
      //   y: "-105%",
      onComplete: () => {
        gsap.set(ref.current, { y: "-150%", opacity: 0 });
      }
    });
  };

  useEffect(() => {
    gsap.set(ref.current, { y: "-150%", opacity: 0 });
  }, []);

  useEffect(() => {
    if (showMenu) {
      ref.current.scrollTo(0, 0);
      activateBlur("menu-blur");
      disableBodyScroll(ref.current);
    } else {
      activateBlur();
      clearAllBodyScrollLocks();
    }
  }, [showMenu]);

  let onClickRow = () => {
    onClose();
  };

  return (
    <Transition in={showMenu} onEnter={onEnter} onExit={onExit} timeout={0}>
      <div className="overlay-menu" ref={ref}>
        <div className="overlay-menu__header container">
          <button onClick={onClose}>
            <Icon icon="close"></Icon>
          </button>
        </div>
        <div className="overlay-menu__nav">
          <BookChaptersUI
            isDynamic={false}
            isEnabled={true}
            isExpanded={true}
            onRowClickCB={onClickRow}
          ></BookChaptersUI>
          {/* <BookChapterNav
            isVisible={true}
            defaultExpanded={true}
            onClickRowCB={onClickRow}
          ></BookChapterNav> */}
        </div>
        <div className="overlay-menu__footer container">
          <a href="" className="icon-link">
            <span>Download this chapter</span> <Icon icon="download"></Icon>
          </a>
          <button
            className="icon-link"
            onClick={() => toggleDarkMode(!darkModeEnabled)}
          >
            <span>Switch to {`darkmode`}</span>
            <Icon icon="darkmode"></Icon>
          </button>
        </div>
      </div>
    </Transition>
  );
}

export default OverlayMenu;
