import { setGlobal, useGlobal, useDispatch } from "reactn";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useRef, useEffect, useState } from "react";
import { disableBodyScroll, clearAllBodyScrollLocks } from "body-scroll-lock";

import { ReactComponent as ImgHowto } from "../../assets/img/ill-howto.svg";
import { ReactComponent as ImgHowtoCommunity } from "../../assets/img/ill-outro.svg";
// import BookChapterNav from "../BookChapterNav";
import Foreword from "./Foreword";
import OverlayWrapper from "../OverlayWrapper";
import Overlay from "components/Overlay";
import animationData01 from "assets/lottie-animations/home-intro.json";
import Lottie from "react-lottie";
import HeaderLogo from "components/HeaderBar/HeaderLogo";
import BookChaptersUI from "components/BookChaptersUI";
import MozfestFooter from "components/MozfestFooter";
gsap.registerPlugin(ScrollTrigger);

function Home() {
  const [showChapterNav, setShowChapterNav] = useGlobal("showChapterNav");
  const ref = useRef(null);
  const headerRef = useRef(null);
  const lottieRef = useRef(null);
  const buttonRef = useRef(null);
  const [collapseNav, setCollapseNav] = useState(false);
  const [hideFakeNav, setHideFakeNav] = useState(false);
  const [centerNav, setCenterNav] = useState(false);
  const [showPreface, setShowPreface] = useState(false);
  const [bookColor, setBookColor] = useGlobal("bookColor");
  const activateBlur = useDispatch("blur");
  const disableHeaderBar = useDispatch("disableHeaderBar");
  const [titlePaused, setTitlePaused] = useState(true);

  const defaultOptions = {
    loop: false,
    autoplay: false,
    animationData: animationData01,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice"
    }
  };

  // Intro animation
  useEffect(() => {
    console.log(lottieRef.current);
    // gsap.set(lottieRef.current.el, { opacity: 0 });

    let timeline = gsap.timeline();
    timeline
      .set(lottieRef.current.el, { opacity: 0 })
      .set(headerRef.current, { opacity: 0 })
      .to(
        headerRef.current,
        { opacity: 1, duration: 1, ease: "power4.inOut" },
        0.2
      )
      .to(
        lottieRef.current.el,
        { opacity: 1, duration: 1, ease: "power4.inOut" },
        0.2
      )
      .add(() => {
        setTitlePaused(false);
      }, "-=0.8");

    // gsap.set([refTitle.current], { opacity: 0 });

    // timeline.to(refNr.current, { scale: 1, delay: 0.3 }).add(() => {
    //   setTitlePaused(false);
    // }, "-=0.2");

    // .set([refTitle.current], { opacity: 0, delay: 0.3 })
    // .to(refTitle.current, { opacity: 1 }, "0.5");

    // timeline.play();

    // return () => {
    //   if (timeline) timeline.kill(true);
    // };
  }, []);

  let introduceMenu = () => {
    let elBookChapters = ref.current.querySelector(".book-chapters");

    let positionFakeMenu = () => {
      setCenterNav(true);
      setCollapseNav(1);
      gsap.set(elBookChapters, { top: "50%", y: "-50%" });
    };

    let replaceWithRealMenu = () => {
      gsap.set(elBookChapters, { opacity: 0 });
      setShowChapterNav(true);
    };

    let reEnableScrolling = () => {
      gsap.to(ref.current, {
        height: "0px",
        duration: 1,
        ease: "Power2.easeOut",
        onComplete: () => {
          clearAllBodyScrollLocks();
        }
      });
    };

    var tl = gsap.timeline({});

    // temporarely disable scrolling
    disableBodyScroll(ref.current);

    // the user probably scrolled to far,
    setTimeout(() => {
      console.log("aaaa");
      window.scrollTo(0, ref.current.offsetTop);
    }, 50);

    tl.call(positionFakeMenu, [], 0.1)
      .call(replaceWithRealMenu, [], 1)
      .call(reEnableScrolling, [], 1);
  };

  // useEffect(() => {
  //   if (showPreface) {
  //     activateBlur('preface-blur');
  //   } else {
  //     activateBlur(false);
  //   }
  // }, [showPreface])

  let onShowPreface = () => {
    // console.log("a");
    setShowPreface(true);
  };

  let onHidePreface = () => {
    setShowPreface(false);
  };

  // load initial data
  useEffect(() => {
    setBookColor("white");
    setShowChapterNav(true);
    disableHeaderBar(true);

    return () => {
      clearAllBodyScrollLocks();
    };
  }, []);

  useEffect(() => {
    let trigger;

    // if (window.matchMedia("(min-width:600px)").matches) {
    //   // trigger if the timeline comes into view
    //   trigger = ScrollTrigger.create({
    //     trigger: ref.current,
    //     start: "top top",
    //     end: "bottom bottom",
    //     once: true,
    //     onEnter: () => {
    //       introduceMenu();
    //     }
    //   });
    // }

    return () => {
      if (trigger) trigger.kill(true);
    };
  }, []);

  return (
    <>
      <article className="home page">
        <div className="home-header" ref={headerRef}>
          <div className="container">
            <HeaderLogo listenToScroll={false}></HeaderLogo>
          </div>
        </div>
        <div className="home-block container home-block--howto ">
          <div>
            <Lottie
              options={defaultOptions}
              isPaused={titlePaused}
              ref={lottieRef}
            />
          </div>
        </div>
        <div className="home-block home-block--quote container">
          <div className="home-block__inner">
            {/* Intro Quote */}
            <blockquote>
              <p>
                At MozFest, people from across the globe  technologists from
                Nairobi, educators from Berlin  come together to build a
                healthier internet. We examine the most pressing issues online,
                like misinformation and the erosion of privacy. Then we roll up
                our sleeves to find solutions. In a way, MozFest is just the
                start: The ideas we bat around and the code we write always
                evolves into new campaigns and new open-source products.
              </p>
              <cite> Mark Surman</cite>
            </blockquote>
            <button
              className="card-style"
              title="Read more about Mozfest"
              onClick={onShowPreface}
            >
              More about this project
            </button>
          </div>
        </div>
        <div className="home-onboard-menu" ref={ref}>
          <div
            className={`somecontainer ${
              centerNav ? "is-vertical-centered" : ""
            }`}
          >
            <BookChaptersUI
              isDynamic={false}
              isEnabled={true}
              isExpanded={true}
            ></BookChaptersUI>
            {/* {!hideFakeNav && (
              <BookChapterNav
                fullCollapse={collapseNav}
                defaultExpanded={true}
                noMouse={true}
              ></BookChapterNav>
            )} */}
          </div>
        </div>
        <div className="home-block home-block--howto-community">
          <ImgHowtoCommunity></ImgHowtoCommunity>
        </div>
      </article>
      <MozfestFooter></MozfestFooter>
      <div className="home-overlay">
        <OverlayWrapper
          isVisible={showPreface}
          onCloseOverlay={onHidePreface}
          slug="preface"
          scrollable={true}
        >
          <Foreword />
        </OverlayWrapper>
      </div>
    </>
  );
}

export default Home;
