import ChapterPage from "./ChapterPage";
import ChapterTitle from "./ChapterTitle";
import { useHistory } from "react-router-dom";
import Elements from "./Elements";
import { useRef, useEffect, useLayoutEffect, useState } from "react";
import { useGlobal } from "reactn";
import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);
gsap.registerPlugin(ScrollToPlugin);

function usePrevious(value) {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
}

function ChapterPagesContinous(props) {
  const { chapter, transitionDone } = props;
  const history = useHistory();

  const [currentChapter, setCurrentChapter] = useGlobal("currentChapter");
  const [currentSubChapter, setCurrentSubChapter] = useGlobal("currentSubChapter");
  const [scrollToPage, setScrollToPage] = useGlobal("scrollToPage");
  const [ignoreLocationChange, setIgnoreLocationChange] = useState(false);

  const [firstLoad, setFirstLoad] = useState(true);
  const [doNotScroll, setDoNotScroll] = useState(false);

  const isOnEnterView = useRef(false);
  const ignoreLocationChng = useRef(false);
  const isAutoScrolling = useRef(false);

  // remember the previous chapter
  const prevSubChapter = useRef(null);

  let onEnterView = (pageSlug) => {
    if (isAutoScrolling.current) return;
    if (pageSlug === prevSubChapter.current) {
      return;
    }

    isOnEnterView.current = true;
    ignoreLocationChng.current = true;

    history.push(`/${chapter.slug}/${pageSlug}`);
    prevSubChapter.current = pageSlug;
  };

  let getPageIndex = (slug) => {
    if (!currentSubChapter) return 0;
    let pageObject = chapter.pages.find((o) => o.slug === slug);
    return chapter.pages.indexOf(pageObject);
  };

  useLayoutEffect(() => {
    // prevent scrolling back to top when visiting another chapter

    if (!transitionDone || !chapter.continuous) return;

    if (!currentSubChapter) return;

    if (isAutoScrolling.current) return;

    if (ignoreLocationChng.current && isOnEnterView.current) {
      ignoreLocationChng.current = false;
      isOnEnterView.current = false;
      return;
    }

    let currentChapterIndex = getPageIndex(currentSubChapter);

    let scrollParams = {
      ease: "power4.inOut",
      duration: 1,
      overwrite: true,
      onComplete: () => {
        // console.log("autoscroll complete");
        isAutoScrolling.current = false;
      }
    };

    console.log("hmmmm");
    // if the target is the first page
    if (currentChapterIndex === 0) {
      isAutoScrolling.current = true;

      // console.log(window.scrollY);
      gsap.to(window, {
        scrollTo: 0,
        ...scrollParams
      });
    } else {
      let element = document.body.querySelector(`[data-slug="${currentSubChapter}"]`);

      if (element) {
        isAutoScrolling.current = true;

        gsap.to(window, {
          scrollTo: element,
          ...scrollParams
        });
      }
    }

    prevSubChapter.current = currentSubChapter;

    return () => {
      ignoreLocationChng.current = true;
    };
  }, [currentSubChapter]);

  return (
    <div className={`chapter__page chapter-page`}>
      <ChapterPage transitionDone={transitionDone}>
        <ChapterTitle chapter={chapter}></ChapterTitle>
        <div className="chapter-page__body">
          <div className="timeline">
            {chapter.pages.map((page, index) => {
              return <TimelineSection key={index} onEnterView={onEnterView} page={page}></TimelineSection>;
            })}
          </div>
        </div>
      </ChapterPage>
    </div>
  );
}

export default ChapterPagesContinous;

// -----------
function TimelineSection(props) {
  const { page, onEnterView } = props;
  const ref = useRef(null);
  const tlHeaderRef = useRef(null);
  const tlContentRef = useRef(null);
  const tlBodyRef = useRef(null);
  const tlLabelRef = useRef(null);
  const tlTitleRef = useRef(null);

  const timelineInitWait = 1000;

  useEffect(() => {
    let tl;
    let trigger;
    let pinner;
    let elHeader = ref.current.querySelector(".timeline-section__header");
    let elLabel = elHeader.querySelector("h3");
    let elContent = tlContentRef.current;
    let elTitle = tlTitleRef.current;
    //   // console.log("re re");
    gsap.set(elContent, { opacity: 0 });
    gsap.set(elLabel, { scale: 5, transformOrigin: "top center" });
    gsap.set(elTitle, {
      scale: 2.4,
      transformOrigin: "top center"
    });

    // pinner = ScrollTrigger.create({
    //   trigger: elHeader,
    //   start: () => {
    //     return `top top`;
    //   },
    //   end: () => {
    //     return `${window.innerHeight / 3}px top`;
    //   },
    //   markers: true
    // });
    tl = gsap.timeline({
      scrollTrigger: {
        trigger: elHeader,
        start: () => {
          return `-=100% top`;

          // return `-${window.innerHeight / 6}px top`;
        },
        end: () => {
          return `${window.innerHeight / 3}px top`;
        },
        onEnter: () => {},
        onToggle: (e) => {
          if (e.isActive) {
            onEnterView(page.slug);
          }
        },
        // markers: true,
        scrub: true,
        onComplete: () => {
          ScrollTrigger.refresh();
        }
      }
    });
    tl.to(elLabel, { scale: 1, duration: 1 }, "0.5").to(elTitle, { scale: 1, duration: 1 }, "1").to(elContent, { opacity: 1, duration: 1 }, "1.5");

    // store the trigger
    trigger = tl.scrollTrigger;

    return () => {
      // if (bodyTween) bodyTween.kill(true);
      if (trigger) trigger.kill(true);

      if (tl) tl.kill(true);

      // if (trigger) trigger.kill(true);
    };
  }, []);

  return (
    <div className="timeline-section" data-slug={page.slug} ref={ref}>
      <TimelineHeader index={page.index}></TimelineHeader>
      <div className="timeline-section__body" ref={tlBodyRef}>
        <div className="timeline-section__title container">
          <h1 ref={tlTitleRef}>{page.title}</h1>
        </div>

        <div className="timeline-section__content" ref={tlContentRef}>
          <Elements elements={page.components}></Elements>
        </div>
      </div>
    </div>
  );
}

let TimelineHeader = (props) => {
  const tlHeaderRef = useRef(null);
  const tlLabelRef = useRef(null);

  useEffect(() => {
    // this is responsible for pinning
    let headerTrigger = ScrollTrigger.create({
      trigger: tlHeaderRef.current,
      start: "top top",
      end: () => {
        return `${window.innerHeight / 3}px top`;
      },
      pin: true
    });

    return () => {
      if (headerTrigger) headerTrigger.kill(true);
    };
  }, []);

  return (
    <div className="timeline-section__header " ref={tlHeaderRef}>
      <div className="container">
        <h3 ref={tlLabelRef}>{props.index}</h3>
      </div>
    </div>
  );
};
