import React, { useState } from "react";
import { BrowserRouter as Router, Switch, matchPath, Route, useLocation, useRouteMatch } from "react-router-dom";
import { useGlobal, useEffect, useRef, useDispatch } from "reactn";

import { gsap } from "gsap";
import { ScrollToPlugin } from "gsap/ScrollToPlugin";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// import ParticipantBioOverlay from "components/Dialogue/BioOverlay";

// import ScrollToTop from "components/ScrollToTop";

function ScrollToTop() {
  const location = useLocation();
  const [pageChange, setPageChange] = useGlobal("pageChange");
  const [pageTransitionDone, setPageTransitionDone] = useGlobal("pageTransitionDone");
  let isChapter = useRouteMatch("/:chapter/:page");

  const previousRouteChapter = useRef(false);

  const isChapterRoute = useRouteMatch({
    path: "/:chapter"
  });

  // useEffect(() => {
  //   console.log(`called when a route changes: ${location.pathname}`);
  //   setPageChange(location.pathname);
  //   setPageTransitionDone(false);
  // }, [location.pathname]);

  useEffect(() => {
    // if the previous route was not a chapter but this one is
    if (!previousRouteChapter.current && isChapterRoute) {
      window.scrollTo(0, 0);
    } else if (!isChapterRoute) {
      window.scrollTo(0, 0);
    }

    previousRouteChapter.current = isChapterRoute;
  }, [location.pathname, isChapterRoute]);

  return null;
}
export default ScrollToTop;
