import { useRef, useEffect, useState } from "react";
import { useGlobal, useDispatch } from "reactn";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import parse from "html-react-parser";

import AuthorAvatar from "components/Author/AuthorAvatar";

gsap.registerPlugin(ScrollTrigger);

function DialogueMoment(props) {
  const { participant, dialogue, onParticipantClick } = props;
  const showAuthorDispatch = useDispatch("showAuthor");
  const [authors] = useGlobal("authors");
  const dialogueRef = useRef(null);
  const author = getAuthor(participant);

  function onClick() {
    showAuthorDispatch(author.id);
  }

  function getAuthor(id) {
    let author = authors.find((o) => o.id === id);
    return author ? author : {};
  }

  // listen for page scrolling
  useEffect(() => {
    let mainTrigger;

    gsap.set([dialogueRef.current], { opacity: 0 });
    gsap.set(dialogueRef.current, { y: "10px" });

    // main trigger
    mainTrigger = ScrollTrigger.create({
      trigger: dialogueRef.current,
      start: "top 90%",
      onToggle: (e) => {
        if (e.isActive) {
          gsap.to(dialogueRef.current, { opacity: 1, y: "0px" });
        }
      }
    });

    return () => {
      if (mainTrigger) mainTrigger.kill(true);
    };
  }, []);

  return (
    <div className="dialogue-moment is-loading" ref={dialogueRef}>
      <div className="dialogue-moment__author">
        <div className="dialogue-moment__avatar" onClick={onClick}>
          <AuthorAvatar {...author.image}></AuthorAvatar>
        </div>
      </div>
      <div className="dialogue-moment__body">
        <div className="dialogue-moment__text">
          {dialogue.map((paragraph, index) => (
            <DialogueParagraph key={index} text={paragraph}></DialogueParagraph>
          ))}
        </div>
      </div>
    </div>
  );
}

export default DialogueMoment;

function DialogueParagraph(props) {
  const ref = useRef();
  const dialogueText = useRef(null);
  const paragraphRef = useRef(null);
  const paragraphLoaderRef = useRef(null);
  function revealText() {
    gsap.to(paragraphLoaderRef.current, { opacity: 0, duration: 0.2 });
    gsap.to(paragraphRef.current, {
      delay: 0.2,
      duration: 0.2,
      opacity: 1,
      y: "0px"
    });
  }

  // listen for page scrolling
  useEffect(() => {
    let mainTrigger;
    let textTrigger;
    let revealTimeout;

    gsap.set([ref.current, paragraphRef.current], { opacity: 0 });
    gsap.set(paragraphRef.current, { y: "10px" });

    // main trigger
    mainTrigger = ScrollTrigger.create({
      trigger: ref.current,
      start: "top 90%",
      onToggle: (e) => {
        if (e.isActive) {
          gsap.to(ref.current, { opacity: 1, y: "0px" });
          revealTimeout = setTimeout(revealText, 2000);
        }
      }
    });

    // text reveal trigger
    textTrigger = ScrollTrigger.create({
      trigger: ref.current,
      start: "top 50%",
      onToggle: (e) => {
        if (e.isActive) {
          clearTimeout(revealTimeout);
          setTimeout(revealText, 250);
        }
      }
    });

    return () => {
      if (mainTrigger) mainTrigger.kill(true);
      if (textTrigger) textTrigger.kill(true);
    };
  }, []);

  return (
    <div className="dialogue-paragraph" ref={ref}>
      <div
        className="dialogue-paragraph__text"
        ref={paragraphRef}
        dangerouslySetInnerHTML={{ __html: props.text }}
      ></div>

      <div className="dialogue-paragraph__loader" ref={paragraphLoaderRef}>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
  );
}
