import { setGlobal, useGlobal, useDispatch } from "reactn";
import { gsap } from "gsap";
import { useRef, useEffect, useState } from "react";
import { Transition } from "react-transition-group";
import Overlay from "./Overlay";

function OverlayWrapper(props) {
  const { isVisible } = props;
  const ref = useRef(null);
  const activateBlur = useDispatch("blur");

  let onEnter = () => {
    gsap.set(ref.current, { opacity: 0 });
    gsap.to(ref.current, { opacity: 1 });
  };

  let onExit = () => {
    gsap.to(ref.current, {
      opacity: 0
    });
  };

  useEffect(() => {
    if (isVisible) {
      activateBlur("preface-blur");
    } else {
      activateBlur(false);
    }

    return () => {
      activateBlur(false);
    };
  }, [isVisible]);

  return (
    <Transition
      in={isVisible}
      mountOnEnter
      unmountOnExit
      onEnter={onEnter}
      onExit={onExit}
      timeout={1000}
    >
      <div ref={ref}>
        <Overlay {...props}>{props.children}</Overlay>
      </div>
    </Transition>
  );
}

export default OverlayWrapper;
