import React, { useState, useEffect, useRef, useCallback } from "react";
import { useLocation, useParams, useRouteMatch } from "react-router-dom";
import Chapters from "../../common/chapters.json";
import { useGlobal, useDispatch } from "reactn"; // <-- reactn
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks
} from "body-scroll-lock";
import useOnClickOutside from "use-onclickoutside";
import useKeypress from "../../hooks/useKeyPress";
import Icon from "../Icon";

import { NavLink, useHistory } from "react-router-dom";
import gsap from "gsap/gsap-core";
import Headroom from "headroom.js";
import { ReactComponent as IconLogo } from "assets/icon-m-logo-box.svg";
import HeaderLogo from "./HeaderLogo";

// ------------------------------------------------------------------------------------------------ //
function IndexPanel(props) {
  let { index, chapter, activeSubChapter, url } = props;
  let hasIndex = index.length > 1;
  let history = useHistory();

  const [chapterIndexOpen, setChapterIndexOpen] = useGlobal("chapterIndexOpen");
  const [scrollToPage, setScrollToPage] = useGlobal("scrollToPage");
  const activateBlur = useDispatch("blur");

  const panelRef = useRef(null);
  const panelLayerRef = useRef(null);

  let getSubChapter = () => {
    if (!chapter.index) return;
    let subChapterObj = chapter.index.find((o) => o.slug == activeSubChapter);
    return subChapterObj;
  };

  let hideOverlay = () => {
    if (!chapterIndexOpen) return;

    // setBookBlur(false);
    activateBlur(false);
    setTimeout(() => {
      setChapterIndexOpen(false);
    }, 200);
  };
  let showOverlay = () => {
    if (chapterIndexOpen) return;

    // setBookBlur(true);
    activateBlur("index-blur");
    setChapterIndexOpen(true);
  };
  let onToggleOverlay = () => {
    if (!hasIndex) return;

    if (!chapterIndexOpen) {
      showOverlay();
    } else {
      hideOverlay();
    }
  };

  let isActive = (section) => {
    return section.slug === activeSubChapter;
  };

  let onItemClick = (sectionSlug) => {
    // need to migrate this somewhere else

    hideOverlay();

    let clickedPage = chapter.pages.find((o) => o.slug === sectionSlug);
    // determine if the user clicked on a page which is also the first page of a chapter
    let isFirstPageClicked = chapter.pages.indexOf(clickedPage) === 0;
    // determine if the user clicked on a page which is already active
    let isCurrentPageClicked =
      activeSubChapter && sectionSlug === activeSubChapter;

    console.log(activeSubChapter);

    setTimeout(() => {
      // scroll to the page
      if (isCurrentPageClicked || (!activeSubChapter && isFirstPageClicked)) {
        setScrollToPage(sectionSlug);
        return;
      }
      // console.log(clickedPage);
      // console.log(activeSubChapter);
      // console.log(isFirstPageClicked);
      // if (sectionSlug)
      history.push(`/${chapter.slug}/${sectionSlug}`);
    }, 400);
  };

  let getActiveTitle = () => {
    if (!subChapter || !chapter) {
      return {};
    }
    let chapterNr = chapter.chapter_nr;
    let chapterTitle = `${subChapter.index}: ${subChapter.title}`;
    return {
      chapterNr,
      chapterTitle
    };
    // return `${chapter.chapter_nr} ${subChapter.index}: ${subChapter.title}`;
  };

  let renderTriggerToggle = () => {
    let label = chapterIndexOpen ? "Close" : "Show all";

    return (
      <button
        className={`index-trigger__toggle ${
          chapterIndexOpen ? "is-enabled" : ""
        }`}
      >
        {chapterIndexOpen}
        <span>{label}</span>
        <Icon icon="arrow-down"></Icon>
      </button>
    );
  };

  useEffect(() => {
    if (chapterIndexOpen) {
      disableBodyScroll(panelRef);

      let navItems = panelLayerRef.current.querySelectorAll("li");
      gsap.set(navItems, { opacity: 1 });
      gsap.set(panelLayerRef.current, { opacity: 0, y: "-50px" });
      gsap.to(panelLayerRef.current, { duration: 0.2, opacity: 1, y: "0px" });

      // gsap.to(panelLayerRef.current, { duration: 0.2, y: "0%" });
      // gsap.to(navItems, { delay: 0.05, opacity: 1, stagger: { each: 0.1 } });
    } else {
      let navItems = panelLayerRef.current.querySelectorAll("li");
      // gsap.to(navItems, { opacity: 0, duration: 0.2 });
      gsap.to(panelLayerRef.current, {
        // delay: 0.2,
        duration: 0.2,
        y: "-50px",
        opacity: 0,
        onComplete: () => {
          gsap.set(panelLayerRef.current, { opacity: 0, y: "-99999px" });
          enableBodyScroll(panelRef);
        }
      });
    }

    return () => {
      clearAllBodyScrollLocks();
    };
  }, [chapterIndexOpen]);

  const subChapter = getSubChapter();

  const { chapterNr, chapterTitle } = getActiveTitle();

  useKeypress("Escape", hideOverlay);
  useOnClickOutside(panelRef, hideOverlay);

  let renderIndexTitle = () => {
    let nr = false;
    let title = false;

    if (chapter) {
      nr = chapter.chapter_nr;
      title = chapter.title;
    }

    if (subChapter) {
      title = `${subChapter.index}: ${subChapter.title}`;
    }

    return (
      <>
        <div className="index-trigger__chapter-nr">{nr}</div>
        <div className="index-trigger__chapter-title">{title}</div>
      </>
    );
  };

  return (
    <div className="index-panel" ref={panelRef}>
      <div
        className="index-panel__trigger index-trigger"
        onClick={onToggleOverlay}
      >
        <div className="index-trigger__current">
          {renderIndexTitle()}
          {/* <div className="index-trigger__chapter-nr">
                {getActiveTitle().chapterNr}
              </div>
              <div className="index-trigger__chapter-title">
                {getActiveTitle().chapterTitle}
              </div> */}
        </div>

        <div className="index-trigger__progress">
          <ScrollProgress></ScrollProgress>
        </div>
        {index.length > 1 && renderTriggerToggle()}
      </div>
      <div
        className={`index-panel__layer ${chapterIndexOpen ? "is-visible" : ""}`}
        ref={panelLayerRef}
      >
        <ul className={`index-panel__list `} tabIndex="0">
          {hasIndex && (
            <>
              {index.map((section, index) => (
                <ListItem
                  key={index}
                  index={section.index}
                  title={section.title}
                  slug={section.slug}
                  isActive={isActive(section)}
                  onItemClick={onItemClick}
                ></ListItem>
              ))}
            </>
          )}
        </ul>
      </div>
    </div>
  );
}

export default IndexPanel;

// ------------------------------------------------------------------------------------------------ //
function ListItem(props) {
  const { index, title, slug, isActive, onItemClick } = props;

  let onClick = () => {
    onItemClick(slug);
  };

  return (
    <li
      role="option"
      className={`${isActive ? "is-active" : ""}`}
      aria-selected={isActive}
    >
      <button onClick={onClick}>
        <Icon icon="hand"></Icon>

        <span>{index}</span>
        <h3>{title}</h3>
      </button>
    </li>
  );
}

// ------------------------------------------------------------------------------------------------ //
// !! improve perfomance, maybe the scroll listener is called too often
function ScrollProgress(props) {
  const [scrollProgress, setScrollProgress] = useState(0);
  const scrollStyle = (progress) => ({
    transform: `scaleX(${progress / 100})`
  });

  useEffect(() => {
    window.addEventListener("scroll", onScroll);

    return () => {
      window.removeEventListener("scroll", onScroll);
    };
  }, []);

  let onScroll = () => {
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop;
    const height =
      document.documentElement.scrollHeight -
      document.documentElement.clientHeight;
    const scrolled = (winScroll / height) * 100;

    setScrollProgress(scrolled);
  };

  return (
    <div className="scroll-progress">
      <div
        className="scroll-progress__bar"
        style={scrollStyle(scrollProgress)}
      ></div>
    </div>
  );
}
