import { useEffect, useState, useRef } from "react";
// import { useRef, useEffect, useState } from "react";

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { SplitText } from "gsap/SplitText";
import useResizeObserver from "@react-hook/resize-observer";

gsap.registerPlugin(ScrollTrigger, SplitText);

// import "../../vendor/SplitText";

gsap.registerPlugin(ScrollTrigger);

function MosaicTitle(props) {
  const { title, color, darkMode } = props;

  const ref = useRef();
  const canvasRef = useRef();
  const canvasCtxRef = useRef();
  const offscreenCanvasRef = useRef();
  const offscreenCanvasCtxRef = useRef();
  const sourceRef = useRef();
  const splittedTextRef = useRef(false);
  const [size, setSize] = useState();

  // detect resize changes
  useResizeObserver(ref, (entry) => setSize(entry.contentRect));

  // prepare canvas context
  useEffect(() => {
    // create offscreen canvas
    offscreenCanvasRef.current = document.createElement("canvas");
    offscreenCanvasCtxRef.current = offscreenCanvasRef.current.getContext("2d");
  }, []);

  useEffect(() => {
    // !!! figure out how to deal with the fact that after darkmode is enabled and this part re-renders
    // the actual text has not yet changed color, and thus i cannot fetch the right color.

    // calc size of canvas
    let srcBnd = sourceRef.current.getBoundingClientRect();
    let width = srcBnd.width;
    let height = srcBnd.height;

    // calculate retina resolution
    var dpr = window.devicePixelRatio || 1;
    let dprWidth = parseInt(width * dpr);
    let dprHeight = parseInt(height * dpr);

    // create offscreen canvas
    const offscreenCanvas = offscreenCanvasRef.current;
    offscreenCanvas.width = dprWidth;
    offscreenCanvas.height = dprHeight;

    const offscreenCanvasCtx = offscreenCanvasCtxRef.current;
    offscreenCanvasCtx.scale(dpr, dpr);

    // create visual canvas
    const canvas = canvasRef.current;
    canvas.width = dprWidth;
    canvas.height = dprHeight;

    const canvasCtx = (canvasCtxRef.current = canvas.getContext("2d"));
    canvas.style.width = width + "px";
    canvas.style.height = height + "px";

    // split the h1 dom text, to create the canvas version.
    // if there already is a splittedTextRef revert to get a splitted line
    if (splittedTextRef.current) {
      splittedTextRef.current.revert();
    }

    splittedTextRef.current = new SplitText(sourceRef.current, {
      type: "lines"
    });
    let splittedText = splittedTextRef.current;

    // get the text styles from the source element
    let textStyles = window.getComputedStyle(splittedText.lines[0]);
    let fontSize = parseInt(textStyles.getPropertyValue("font-size"));
    let fontFamily = textStyles.getPropertyValue("font-family");
    let fontLineHeight = parseInt(textStyles.getPropertyValue("line-height"));
    let fontColor = textStyles.getPropertyValue("color");

    // set the font
    offscreenCanvasCtx.font = `${fontSize}px ${fontFamily}`;
    offscreenCanvasCtx.textAlign = "center";
    offscreenCanvasCtx.fillStyle = fontColor;

    // walk through the textlines and draw text
    splittedText.lines.forEach((line, index) => {
      let text = line.textContent;
      offscreenCanvasCtx.fillText(
        text,
        width / 2,
        fontLineHeight * index + fontLineHeight / 1.25
      );
    });

    // copy image from offscreenCanvas
    let offscreenData = offscreenCanvasCtx.getImageData(
      0,
      0,
      dprWidth,
      dprHeight
    );

    // keep track of current sample size to prevent re-renders
    let currentSampleSize = 0;
    let lowresSampleSize = 30;
    let nearestRoundingSize = 2;

    // Renders a pixelated version of the text
    let renderVisual = (sampleSize = 1, minSampleSize = 4) => {
      currentSampleSize = sampleSize;

      // draw image to canvas
      canvasCtx.clearRect(0, 0, dprWidth, dprHeight);

      if (sampleSize < minSampleSize) {
        sampleSize = minSampleSize;
      }

      let sample = offscreenData.data;

      for (var y = 0; y < dprHeight; y += sampleSize) {
        for (var x = 0; x < dprWidth; x += sampleSize) {
          var pos = (x + y * dprWidth) * 4;
          var red = sample[pos];
          var green = sample[pos + 1];
          var blue = sample[pos + 2];

          var alpha = sample[pos + 3];

          canvasCtx.beginPath();
          canvasCtx.fillStyle =
            "rgba(" + red + "," + green + "," + blue + "," + alpha + ")";

          canvasCtx.rect(x, y, sampleSize, sampleSize);
          canvasCtx.fill();
        }
      }
    };

    let trigger = ScrollTrigger.create({
      trigger: canvasRef.current,
      start: "center bottom",
      end: "center top",
      // markers: true,
      onUpdate: (self) => {
        let value = lowresSampleSize * Math.abs(self.progress - 0.5);
        let valueRounded =
          Math.round(value / nearestRoundingSize) * nearestRoundingSize; // round it to the nearest 10

        if (valueRounded !== currentSampleSize) {
          renderVisual(valueRounded);
        }
      }
    });

    //
    renderVisual(lowresSampleSize);

    return () => {
      if (trigger) trigger.kill(true);
    };
  }, [size, darkMode]);

  return (
    <div className="mosaic-title" ref={ref}>
      <h1 ref={sourceRef}>{title}</h1>

      <canvas id="canvas" ref={canvasRef}></canvas>
    </div>
  );
}

export default MosaicTitle;
