import { useRef, useEffect, useLayoutEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Image from "../Image";
import GetParentScrollContainer from "../../common/GetParentScrollContainer";

gsap.registerPlugin(ScrollTrigger);

function ElementTextImages(props) {
  const { text, images } = props;

  const image = images[0];
  const imageRef = useRef(null);
  const textRef = useRef(null);
  const sizerPortrait = useRef(null);
  const sizerLanscape = useRef(null);

  let revealCaption = () => {
    if (!imageRef.current) return;

    let figcaptionEl = imageRef.current.querySelector("figcaption");
    if (!figcaptionEl) return;

    gsap.to(figcaptionEl, { opacity: 1 });
  };

  useLayoutEffect(() => {
    if (!window.matchMedia("(min-width:600px)").matches) return;

    let timeline;
    let trigger;

    if (imageRef) {
      console.log("oke!");
      let scrollContainer = GetParentScrollContainer(imageRef.current);

      let figcaptionEl = imageRef.current.querySelector("figcaption");
      let startScale =
        sizerLanscape.current.offsetWidth / imageRef.current.offsetWidth;
      let imgWidth = imageRef.current.offsetWidth;
      let imgHeight = imageRef.current.offsetHeight;

      let newImgHeight = imgHeight * startScale;

      // hide figcaption
      if (figcaptionEl) {
        gsap.set(figcaptionEl, { opacity: 0 });
      }

      gsap.set(imageRef.current, {
        transformOrigin: "top left",
        scale: startScale,
        y: `-${(newImgHeight / imgHeight) * 100}%`
      });

      timeline = gsap.timeline({
        onComplete: revealCaption,
        scrollTrigger: {
          scroller: scrollContainer,
          trigger: imageRef.current,
          start: "bottom bottom",
          scrub: 0.2,
          once: true
          // onScrubComplete: revealCaption
        },
        ease: "linear"
      });
      timeline
        .set(imageRef.current, {
          transformOrigin: "top left",
          scale: startScale,
          y: `-${(newImgHeight / imgHeight) * 100}%`
        })
        .to(imageRef.current, { y: "0%", ease: "linear" })
        .to(imageRef.current, { scale: 1, ease: "linear" });

      // store the trigger
      trigger = timeline.scrollTrigger;
    }

    return () => {
      if (trigger) trigger.kill(true);
      if (timeline) timeline.kill(true);
    };
  }, []);

  return (
    <div className="component component--text-images container">
      <div className="text-images">
        <div
          className="text-images__sizer text-images__sizer--portrait"
          ref={sizerPortrait}
        ></div>
        <div
          className="text-images__sizer text-images__sizer--landscape"
          ref={sizerLanscape}
        ></div>

        <div className="text-images__image">
          <Image keepAspect="true" {...image} ref={imageRef}></Image>
        </div>
      </div>
    </div>
  );
}

export default ElementTextImages;
