import { useRef, useEffect, useLayoutEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

function ElementTextScaling(props) {
  const ref = useRef(null);
  const sizerScaledRef = useRef(null);
  const sizerNormalRef = useRef(null);

  useLayoutEffect(() => {
    if (!window.matchMedia("(min-width:600px)").matches) return;

    let triggers = [];

    let scaledWidth = sizerScaledRef.current.offsetWidth;
    let normalWidth = sizerNormalRef.current.offsetWidth;

    gsap.set(ref.current, { scale: scaledWidth / normalWidth });

    // let trigger = ScrollTrigger.create({
    //   trigger: ref.current,
    //   start: "top 70%",
    //   // markers: true,
    //   once: true,
    //   onToggle: (e) => {
    //     if (e.isActive) {
    //       gsap.to(ref.current, { scale: 1 });
    //     }
    //   }
    // });

    let tween = gsap.to(ref.current, {
      scale: 1,
      scrollTrigger: {
        trigger: ref.current,
        start: "top 70%",
        scrub: true
        // onScrubComplete: revealCaption
      }
    });

    // kill
    return () => {
      if (tween) tween.kill(true);
    };
  }, []);

  return (
    <section className="component component--text-scaling container">
      <div className="scaling-text">
        <span className="scaling-text__scaled" ref={sizerScaledRef}></span>
        <span className="scaling-text__normal" ref={sizerNormalRef}></span>

        <div
          className="scaling-text__text text "
          dangerouslySetInnerHTML={{ __html: props.text }}
          ref={ref}
        ></div>
      </div>
    </section>
  );
}

export default ElementTextScaling;

function ElementBullets(props) {
  const { bullets } = props;
  const ref = useRef(null);

  useLayoutEffect(() => {
    const bullets = ref.current.querySelectorAll("li");
    let triggers = [];

    bullets.forEach((element) => {
      let trigger = ScrollTrigger.create({
        trigger: element,
        start: "top center",
        onToggle: (e) => {
          if (e.isActive) {
            e.trigger.classList.add("is-active");
          } else {
            e.trigger.classList.remove("is-active");
          }
        }
      });

      triggers.push(trigger);
    });

    // kill
    return () => {
      triggers.forEach((trigger) => {
        trigger.kill(true);
      });
    };
  }, []);

  return (
    <div className="component component--bullets container">
      <ul className="bullets-hands" ref={ref}>
        {bullets.map((bullet, index) => (
          <li key={index} dangerouslySetInnerHTML={{ __html: bullet }}></li>
        ))}
      </ul>
    </div>
  );
}
