// import Example from "./Example";
import {} from "components/globalState";
import React, { useState } from "react";

import { useGlobal, useEffect, useDispatch } from "reactn";
import ParticipantBioOverlay from "./AuthorOverlay";

// Responsible for showing the authors in an overlay based on state
function AuthorsManager(props) {
  const [showAuthor] = useGlobal("showAuthor");
  const [authors] = useGlobal("authors");
  const [visible, setVisible] = useState(false);
  const [author, setAuthor] = useState({});
  const showAuthorDispatch = useDispatch("showAuthor");

  let onCloseOverlay = () => {
    setVisible(false);
    showAuthorDispatch(false);
  };

  useEffect(() => {
    let getAuthor = (id) => {
      let author = authors.find((o) => o.id === id);
      return author ? author : {};
    };

    if (showAuthor) {
      setAuthor(getAuthor(showAuthor));
      setVisible(true);
    } else {
      setVisible(false);
    }
  }, [showAuthor, authors]);

  return (
    <ParticipantBioOverlay
      isVisible={visible}
      participant={author}
      onCloseOverlay={onCloseOverlay}
    ></ParticipantBioOverlay>
  );
}

export default AuthorsManager;
