import MosaicTitle from "./MosaicTitle";
import { useState } from "react";
import { setGlobal, useGlobal, useDispatch } from "reactn";
// import { useGlobal, useDispatch } from "reactn";

import gsap from "gsap/gsap-core";
import { ReactComponent as Logo } from "../../assets/chapter-number.svg";
import Elements from "./Elements";
import AuthorLink from "components/Author/AuthorLink";

function ConversationSubtitle(props) {
  const { page, chapterNr } = props;

  let author_1;
  let author_2;

  if (page.meta.authors.length > 1) {
    let authorData1 = page.meta.authors[0];
    let authorData2 = page.meta.authors[1];

    author_1 = (
      <AuthorLink name={authorData1.name} slug={authorData1.id}></AuthorLink>
    );
    author_2 = (
      <AuthorLink name={authorData2.name} slug={authorData2.id}></AuthorLink>
    );
  }

  return (
    <span className="tagline tagline--conversation">
      {author_1}
      <span className="italic">&nbsp;talks with&nbsp;</span>
      {author_2}
    </span>
  );
}

function SectionTitle(props) {
  const { page, chapterNr } = props;
  const [titleColor, setTitleColor] = useState({ r: 255, g: 255, b: 255 });
  const [darkMode] = useGlobal("darkMode");
  let renderTitle = () => {
    switch (chapterNr) {
      case "05":
        return (
          <>
            <ConversationSubtitle page={page}></ConversationSubtitle>
            <MosaicTitle title={page.title} darkMode={darkMode}></MosaicTitle>
          </>
        );
        break;
      case "06":
        const { authors } = page.meta;

        let createSubtitle = () => {
          if (!authors.length) return null;
          let authorButtons = authors.map((o, key) => {
            return (
              <AuthorLink key={key} name={o.name} slug={o.id}></AuthorLink>
            );
          });

          const list = (data) =>
            data.reduce((prev, curr) => [prev, " and ", curr]);
          return <>By {list(authorButtons)} </>;
        };

        return (
          <>
            <span className="tagline">{page.index}</span>
            <h1>{page.title}</h1>
            <span className="subtitle">{createSubtitle()}</span>
          </>
        );
        break;
      default:
        return (
          <>
            <span className="tagline">{page.index}</span>
            <h1>{page.title}</h1>
            {page.subtitle && <span className="subtitle">{page.subtitle}</span>}
          </>
        );
    }
  };

  return <header className="page-title container">{renderTitle()}</header>;
}

function Section(props) {
  const { page, chapter } = props;
  const { chapter_nr } = chapter;
  const showAuthorDispatch = useDispatch("showAuthor");

  let seperateRender = () => {
    switch (chapter_nr) {
      case "06":
        return (
          <>
            <SectionTitle
              page={page}
              chapterNr={chapter.chapter_nr}
            ></SectionTitle>
            <section className="component component--text container">
              <aside>
                {page.meta.theme && (
                  <>
                    <h4>THEME</h4>
                    <p>{page.meta.theme}</p>
                  </>
                )}

                {page.meta.authors && (
                  <>
                    <h4>AUTHORS</h4>
                    {page.meta.authors.map((author, index) => (
                      <button
                        key={index}
                        onClick={() => showAuthorDispatch(author.id)}
                      >
                        {author.name}
                      </button>
                    ))}
                  </>
                )}
              </aside>

              <div
                className="text"
                dangerouslySetInnerHTML={{ __html: page.text }}
              ></div>
            </section>
            <Elements elements={page.components}></Elements>
          </>
        );
        break;
      default:
        return (
          <>
            {!page.settings.hideTitle && (
              <SectionTitle
                page={page}
                chapterNr={chapter.chapter_nr}
              ></SectionTitle>
            )}
            <Elements elements={page.components}></Elements>
          </>
        );
        break;
    }
  };
  return <>{seperateRender()}</>;
}

export default Section;
