import { useRef, useEffect, useState } from "react";
import { useGlobal } from "reactn";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Dialogue from "../Dialogue/index";
gsap.registerPlugin(ScrollTrigger);

// ---------------------------------------------------------------- //
function InlineFoldout(props) {
  const { name, position, text } = props;
  const [expanded, setExpanded] = useState(false);

  let onClick = () => {
    setExpanded(!expanded);
  };

  return (
    <div
      className={`inline-foldout ${expanded ? "is-expanded" : ""}`}
      onClick={onClick}
      role="dialog"
    >
      <div className="inline-foldout__meta">
        <span>{name}</span>
        <span>{position}</span>
      </div>
      <div className="inline-foldout__panel foldout-panel">
        <div className="foldout-panel__inner">
          <div className="foldout-panel__wrapper">{text}</div>
        </div>
        <div className="foldout-panel__footer">
          <button>Read more</button>
        </div>
      </div>
    </div>
  );
}
function DoubleColumnSection(props) {
  const { text, foldout } = props;

  return (
    <div className="double-column">
      <div className="double-column__text">{text}</div>
      <div className="double-column__quote">
        {foldout && (
          <InlineFoldout
            name={foldout.name}
            position={foldout.position}
            text={foldout.text}
          ></InlineFoldout>
        )}
      </div>
    </div>
  );
}

function AuthorAvatar(props) {
  const { src, alt } = props;

  var style = {
    backgroundImage: `url(${src})`
  };
  return (
    <div className="participant-avatar">
      <figure>
        <img style={style} alt={alt}></img>
      </figure>
    </div>
  );
}
function DialogueMoment(props) {
  const { participant, dialogue, onParticipantClick } = props;

  const dialogueRef = useRef(null);
  const dialogueText = useRef(null);
  const dialogueLoader = useRef(null);

  let revealText = () => {
    gsap.to(dialogueLoader.current, { opacity: 0, duration: 0.2 });
    gsap.to(dialogueText.current, { delay: 0.2, opacity: 1, y: "0px" });
  };

  let onClick = () => {
    onParticipantClick(participant.id);
  };

  // listen for page scrolling
  useEffect(() => {
    let mainTrigger;
    let textTrigger;
    let revealTimeout;

    gsap.set([dialogueRef.current, dialogueText.current], { opacity: 0 });
    gsap.set(dialogueRef.current, { y: "10px" });
    gsap.set(dialogueText.current, { y: "10px" });

    // main trigger
    mainTrigger = ScrollTrigger.create({
      trigger: dialogueRef.current,
      start: "top 90%",
      // markers: true,
      onToggle: (e) => {
        if (e.isActive) {
          gsap.to(dialogueRef.current, { opacity: 1, y: "0px" });
          revealTimeout = setTimeout(revealText, 2000);
        }
      }
    });

    // text reveal trigger
    textTrigger = ScrollTrigger.create({
      trigger: dialogueRef.current,
      start: "top 50%",
      // markers: true,
      onToggle: (e) => {
        if (e.isActive) {
          revealText();
          clearTimeout(revealTimeout);
        }
      }
    });

    return () => {
      if (mainTrigger) mainTrigger.kill(true);
      if (textTrigger) textTrigger.kill(true);
    };
  }, []);

  return (
    <div className="dialogue-moment is-loading" ref={dialogueRef}>
      <div className="dialogue-moment__author">
        <div className="dialogue-moment__avatar" onClick={onClick}>
          <AuthorAvatar {...participant.image}></AuthorAvatar>
        </div>
      </div>
      <div className="dialogue-moment__body">
        <div
          className="dialogue-moment__text"
          ref={dialogueText}
          dangerouslySetInnerHTML={{ __html: dialogue }}
        ></div>
        <div className="dialogue-moment__loader" ref={dialogueLoader}>
          <div></div>
          <div></div>
          <div></div>
        </div>
      </div>
    </div>
  );
}

