function InlineFoldout(props) {
  const { name, position, text } = props;
  const [expanded, setExpanded] = useState(false);
  const [clamped, setClamped] = useState(false);

  const refContainer = useRef();
  const refPanel = useRef();
  const refSizer = useRef();
  const refPanelInner = useRef();
  const collapsedSize = useRef;

  const fullDimension = useRef();

  const [theText, setTheText] = useState(props.text);

  let onClick = () => {
    setExpanded(!expanded);
  };

  useEffect(() => {
    fullDimension.current = {
      width: refPanelInner.current.offsetWidth,
      height: refPanelInner.current.offsetHeight
    };
    // gsap.set(refPanelInner.current, { height: "100px", width: "100px" });
  }, []);

  useEffect(() => {
    if (expanded) {
      setClamped(false);

      gsap.to(refPanelInner.current, {
        height: fullDimension.current.height,
        width: fullDimension.current.width,
        duration: 0.3,
        ease: "Power4.easeOut"
      });

      refContainer.current.focus();
    } else {
      gsap.to(refPanelInner.current, {
        height: refSizer.current.offsetHeight,
        width: refSizer.current.offsetWidth,
        duration: 0.3,
        ease: "Power4.easeOut",
        onComplete: () => {
          setClamped(true);
        }
      });
    }
  }, [expanded]);

  return (
    <div
      className={`inline-foldout ${expanded ? "is-expanded" : ""} ${
        clamped ? "is-clamped" : ""
      }`}
      onClick={onClick}
      role="dialog"
      ref={refContainer}
      aria-expanded={expanded}
    >
      <div className="inline-foldout__meta">
        <span className="meta__name">{name}</span>
        <span className="meta__position">{position}</span>
      </div>
      <div className="inline-foldout__sizer" ref={refSizer}></div>
      <div className="inline-foldout__panel foldout-panel" ref={refPanel}>
        <div className="foldout-panel__inner" ref={refPanelInner}>
          <div
            className="foldout-panel__wrapper"
            dangerouslySetInnerHTML={{ __html: theText }}
          ></div>
        </div>
        <div className="foldout-panel__footer">
          <button>{`${expanded ? "Close" : "Read more"}`}</button>
        </div>
      </div>
    </div>
  );
}

function ParallaxImage(props) {
  const { image } = props;

  const imageRef = useRef(null);
  const [lightboxImage, setLightboxImage] = useState(false);
  const [appBlur, setAppBlur] = useGlobal("appBlur");
  const [imageBnd, setImageBnd] = useState(false);
  useEffect(() => {
    // setInterval(() => {
    //   let bnd = imageRef.current.getBoundingClientRect();
    //   // window.pageYOffset + el.getBoundingClientRect().top
    //   console.log(window.pageYOffset);
    //   // ScrollTrigger.refresh();
    // }, 2000);
    gsap.set(imageRef.current, { left: `${80 * Math.random()}%` });
    let tween = gsap.to(imageRef.current, {
      yPercent: -100,
      ease: "none",
      scrollTrigger: {
        trigger: imageRef.current,
        start: "top bottom",
        end: "bottom top",

        // start: "top bottom", // the default values
        // end: "bottom top",
        scrub: true
      }
    });

    return () => {
      if (tween) tween.kill(true);
    };
    // setChapterIndexOpen(false);
  }, []);
  // gsap.to(".pImage", {
  //   yPercent: 50,
  //   ease: "none",
  //   scrollTrigger: {
  //     trigger: ".pSection",
  //     // start: "top bottom", // the default values
  //     // end: "bottom top",
  //     scrub: true
  //   },
  // });

  let onClick = () => {
    // setAppBlur(true);

    setImageBnd(imageRef.current.getBoundingClientRect());
    setLightboxImage(true);
  };
  let onClose = () => {
    setLightboxImage(false);
  };
  let getImage = () => {
    return imageRef.current;
  };
  return (
    <>
      <div onClick={onClick}>
        <Image
          ref={imageRef}
          keepAspect="true"
          {...image}
          onClick={onClick}
          caption={false}
        ></Image>
      </div>
      <OverlayLightbox
        onClose={onClose}
        visibleToggle={lightboxImage}
        clickedImageRef={imageRef}
        image={image}
        getImage={getImage}
      ></OverlayLightbox>
    </>
  );
}

function OverlayLightbox(props) {
  const { visibleToggle, onClose, image, clickedImageRef } = props;
  const [visible, setVisible] = useState(props.visibleToggle);
  const [scale, setScale] = useState(1);
  const transImageRef = useRef(null);
  const imageRef = useRef(null);
  const ref = useRef(null);

  let options = {
    minScale: 0,
    centerContent: true,
    limitToBounds: false
  };

  let wheelOptions = {
    disabled: true
  };
  let defaultScale = 1;

  let transImageStyle = {
    position: "absolute"
  };

  let getEnlargedScale = () => {
    let wHeight = window.innerHeight;
    let wWidth = window.innerWidth;

    let padding = 60;

    let startDimensions = { w: image.width, h: image.height };
    let adjustedOptions = {};
    let adjustedScale;

    let scale = 1;

    if (image.height < wHeight - padding && image.width < wWidth - padding) {
      scale = 1;
    } else {
      let calcScale;

      // !! also adhere to wide images
      if (image.height >= wHeight - padding) {
        calcScale = (wHeight - padding * 2) / image.height;
      } else if (image.width >= wWidth - padding) {
        calcScale = (wWidth - padding * 2) / image.width;
      }

      startDimensions = {
        w: startDimensions.w * calcScale,
        h: startDimensions.h * calcScale
      };

      scale = calcScale;
    }

    return scale;
  };

  let onEnter = () => {
    // console.log("onEter");
    gsap.set(transImageRef.current, { opacity: 1, position: "absolute" });
    gsap.set(imageRef.current, { opacity: 0 });
    gsap.set(clickedImageRef.current, { opacity: 0 });

    let imageBnd = clickedImageRef.current.getBoundingClientRect();

    let toScale = getEnlargedScale();

    let start = {
      x: imageBnd.x + "px",
      y: imageBnd.y + "px",
      scale: imageBnd.width / image.width
    };

    // position the scalable image
    gsap.set(imageRef.current, {
      opacity: 0,
      x: window.innerWidth / 2 - image.width / 2 + "px",
      y: window.innerHeight / 2 - image.height / 2 + "px",
      transformOrigin: "center center",
      scale: toScale
    });

    // do the transition
    // initial
    gsap.set(transImageRef.current, {
      ...start,
      transformOrigin: "top left"
    });

    //
    gsap.to(transImageRef.current, {
      x: window.innerWidth / 2 - (image.width * toScale) / 2 + "px",
      y: window.innerHeight / 2 - (image.height * toScale) / 2 + "px",
      scale: toScale,
      duration: 0.2,
      onComplete: () => {
        gsap.set(imageRef.current, { opacity: 1 });
        gsap.set(transImageRef.current, { opacity: 0 });
      }
    });
    return;
  };

  let onExit = () => {
    let imageBnd = clickedImageRef.current.getBoundingClientRect();

    gsap.set(transImageRef.current, { opacity: 1 });
    gsap.set(imageRef.current, { opacity: 0 });

    gsap.to(transImageRef.current, {
      x: imageBnd.x + "px",
      y: imageBnd.y + "px",
      scale: imageBnd.width / image.width,
      duration: 0.2,
      onComplete: () => {
        gsap.set(clickedImageRef.current, { opacity: 1 });
        gsap.set(transImageRef.current, { opacity: 0 });
      }
    });
  };

  useEffect(() => {
    setVisible(visibleToggle);
  }, [visibleToggle]);

  // prepare image size

  // lock scrolling when element appears
  useEffect(() => {
    if (visible) {
      disableBodyScroll(ref.current);
    } else {
      clearAllBodyScrollLocks();
    }

    return () => {
      clearAllBodyScrollLocks();
    };
  }, [visible]);

  useKeypress("Escape", onClose);
  return (
    <Transition
      in={visible}
      onEnter={onEnter}
      onExit={onExit}
      timeout={500}
      mountOnEnter
      unmountOnExit
    >
      <div className="overlay-lightbox" onClick={onClose} ref={ref}>
        <div>
          <img
            src={image.src}
            alt={image.alt}
            ref={transImageRef}
            className="transition-image"
            aria-hidden="true"
          ></img>
          <img
            src={image.src}
            alt={image.alt}
            ref={imageRef}
            // style={transImageStyle}
          />
        </div>
      </div>
    </Transition>
  );
}
function DoubleColumnSection(props) {
  const { text, image, foldout } = props;
  return (
    <div className="double-column">
      <div className="double-column__text">
        <div className="text" dangerouslySetInnerHTML={{ __html: text }}></div>
        {image && <ParallaxImage image={image}></ParallaxImage>}
      </div>
      <div className="double-column__quote">
        {foldout && (
          <InlineFoldout
            name={foldout.name}
            position={foldout.position}
            text={foldout.text}
          ></InlineFoldout>
        )}
      </div>
    </div>
  );
}

function ElementDoubleColumn(props) {
  // console.log(props);
  const { content } = props;
  return (
    <div className="component component--double-column container">
      {content.map((content, index) => (
        <DoubleColumnSection
          key={index}
          foldout={content.foldout}
          text={content.text}
          image={content.image}
        ></DoubleColumnSection>
      ))}
    </div>
  );
}

export default ElementDoubleColumn;
